import React from "react";
import { AiFillDashboard } from "react-icons/ai";
import { MdBrightness1 } from "react-icons/md";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../actions";
import { GetApplicationPerPeriod } from "../../actions/dashboard";
import { PopupAlertData } from "../../components/Alert/Popup";
import { SessionPlanInterface } from "../../interfaces";
import { education_category } from "../../interfaces/education_category";
import { StoreState } from "../../reducers";
import { FC_HasInstitute } from "../../utils/functions";

type DashBoardTitleProps = {
  reportTitle: string;
  education_categories: education_category[] | null;
  loading: boolean;
  loadingApplications: boolean;
  loadingEducation: boolean;
  thisEducation: education_category | null;
  allEducation: boolean;
  periodChanged?: boolean;
  setThisEducation: (value: education_category | null) => void;
  thisPeriod: string;
  setThisPeriod: (value: string) => void;
  setPeriodChanged?: React.Dispatch<React.SetStateAction<boolean>>;
  setLoadingApplications: (value: boolean) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
  application_sessions: SessionPlanInterface[] | null;
  FC_OnInstitutionChanged: (institution_id: string) => void;
  FC_OnSessionChanged: (session_id: string) => void;

  auth: Auth;
};

const DashBoardTitle: React.FC<DashBoardTitleProps> = (props) => {
  return (
    <div className="mt-3 rounded flex flex-col md:flex-row md:justify-between items-start">
      <div className="flex flex-row items-center gap-3 mb-2">
        <AiFillDashboard className="text-6xl text-blue-600 hidden md:block" />
        <div className="flex flex-col">
          <span className="text-blue-600 text-3xl font-bold">
            {props.reportTitle}
          </span>
        </div>
      </div>

      {/* FILTER SECTION PERIOD && EDUCATION */}
      <section className="w-full md:w-auto my-2 md:my-0">
        {/* EDUCATION CATEGORY sections */}
        <section>
          {FC_HasInstitute(props.auth.education_institution_id) === false &&
            props.education_categories &&
            props.education_categories.length > 0 && (
              <div className="flex items-start md:items-center justify-start md:justify-end gap-2">
                {props.education_categories.map((education, i) => (
                  <div
                    key={i + education.education_institute_id}
                    onClick={() => {
                      !props.loading &&
                        !props.loadingEducation &&
                        props.setThisEducation(education);
                      !props.loading &&
                        !props.loadingEducation &&
                        props.FC_OnInstitutionChanged(
                          education.education_institute_id
                        );
                    }}
                    className={`w-auto ${
                      props.loading || props.loadingEducation
                        ? "cursor-not-allowed"
                        : "cursor-pointer"
                    } ${
                      props.thisEducation &&
                      education.education_institute_id ===
                        props.thisEducation.education_institute_id
                        ? "bg-blue-500 text-white font-bold"
                        : "bg-gray-100 text-blue-800"
                    } mb-1 hover:font-bold py-1 hover:bg-blue-400 hover:text-white --font-bold  px-2 rounded-md flex items-center animate__animated animate__zoomIn`}
                  >
                    <div className="flex-1">
                      <MdBrightness1 className="inline-block mr-1 text-xs" />
                      <span>{education.education_category_title}</span>
                    </div>
                  </div>
                ))}
              </div>
            )}
        </section>
        {/* end EDUCATION CATEGORY sections */}
        <div className="mb-2">
          <select
            className={`bg-white text-gray-600 border border-gray-400 rounded p-2 focus:outline-none ${
              (props.loadingApplications ||
                props.application_sessions === null) &&
              "cursor-wait"
            }`}
            value={props.thisPeriod}
            disabled={
              props.loading ||
              props.loadingApplications ||
              props.loadingEducation ||
              props.application_sessions === null
            }
            onChange={(e) => {
              props.setThisPeriod(e.target.value);
              props.setLoadingApplications(true);
              props.FC_OnSessionChanged(e.target.value);
            }}
          >
            {props.application_sessions &&
              props.application_sessions.map((session, item) => (
                <option key={session._id + item} value={session._id}>
                  {session.session_plan_name}
                </option>
              ))}
          </select>
        </div>
      </section>
      {/* END FILTER SECTION PERIOD && EDUCATION */}
    </div>
  );
};

const mapStateToProps = ({
  auth,
}: StoreState): {
  auth: Auth;
} => {
  return {
    auth: auth,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    GetApplicationPerPeriod,
    PushPopupAlert,
  })(DashBoardTitle)
);
