import React, { FC } from "react";
import { IconType } from "react-icons";
import { FaUsers } from "react-icons/fa";
import { toAnyDigit } from "../../shared/dataTest";
import { displayNumbers } from "../../utils/functions";

export type DashboardLabelItemInterface = {
  // TITLE: Application or Exam or Shortlisted ....
  title: string;
  // ICON: FaExpand ....
  icon: IconType;
  // ICON COLOR: blue, indigo, gray ....
  iconColor: string;
  // COUNT: 0,1,2,3,5,23,563 ....
  count: number;
  // COUNT: 0,1,2,3,5,23,563 ....
  countOver?: number;
};

const DashboardLabelItem: FC<DashboardLabelItemInterface> = (
  props = {
    title: "",
    icon: FaUsers,
    iconColor: "indigo",
    count: 0,
    countOver: undefined,
  }
) => {
  const IconDefined = props.icon;
  return (
    <div className="flex items-center px-6 py-4 bg-white w-full rounded-md space-x-2 mb-2 lg:mb-0 shadow-md">
      <div
        className={`p-3 rounded-full bg-${props.iconColor}-200 bg-opacity-75`}
      >
        <IconDefined className={`h-8 w-8 text-${props.iconColor}-600`} />
      </div>
      <div>
        <h1 className="text-3xl font-semibold text-gray-700">
          {props.count < 1000
            ? toAnyDigit(props.count)
            : displayNumbers(props.count)}{" "}
          {props.countOver && "/ " + toAnyDigit(props.countOver)}
        </h1>
        <span className="text-gray-500 text-lg">{props.title}</span>
      </div>
    </div>
  );
};

export default React.memo(DashboardLabelItem);
