import React, { Component } from "react";
import { BiSort } from "react-icons/bi";
import { ImFilesEmpty } from "react-icons/im";
import { connect } from "react-redux";
import {
  Auth,
  courseStoreInterface,
  loadEducationCategories,
  GetApplicationPerPeriod,
  loadCourses,
  PushPopupAlert,
  DashboardCountType,
  FC_GetApplicationCountPerPeriod,
  DashboardGeneralCountType,
  FC_GetApplicationGeneralCountPerPeriod,
} from "../../actions";
import { getAllRoom } from "../../actions/location";
import { FC_GetAllSessionByInstitution } from "../../actions/session";
import { FC_GetPositionsByInstitution } from "../../actions/settings";
import { PopupAlertData } from "../../components/Alert/Popup";
import {
  education_category,
  PositionAPI,
  SessionPlanInterface,
} from "../../interfaces";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { DashboardReducerType } from "../../reducers/dashboard";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import { RoomLocationInterface } from "../../shared/interfaces";
import { errorToText } from "../../utils/errors";
import { FC_HasInstitute, sortThisArray } from "../../utils/functions";
import NoSession from "../SessionSettings/NoSession";
import DashboardLabels from "./DashboardLabels";
import DashBoardTitle from "./DashBoardTitle";

interface AppProps {
  auth: Auth;
  locations: RoomLocationInterface[];
  education_categories: education_category[] | null;
  dashboard: DashboardReducerType;
  systemPositions: PositionAPI[] | null;
  application_sessions: SessionPlanInterface[] | null;
  course: courseStoreInterface;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  getAllRoom: Function;
  FC_GetAllSessionByInstitution: Function;
  FC_GetPositionsByInstitution: Function;
  GetApplicationPerPeriod: Function;
  loadCourses: (callback: (loading: boolean) => void) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface AppState {
  loading: boolean;
  loadingEducation: boolean;
  loadingApplications: boolean;
  thisEducation: education_category | null;
  thisPeriod: string;
  chooseSession: boolean;
  applicationsCounts: DashboardCountType[] | null;
  applicationsGeneralCounts: DashboardGeneralCountType | null;
  sortProperty: string;
  isSorted: boolean;
  sortOrder: "ASC" | "DESC";
}

class Index extends Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: true,
      loadingEducation: true,
      loadingApplications: false,
      thisEducation: null,
      thisPeriod: "",
      chooseSession: true,
      applicationsCounts: null,
      sortProperty: "",
      isSorted: false,
      sortOrder: "ASC",
      applicationsGeneralCounts: null,
    };
  }

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };
  setLoadingEducation = (value: boolean) => {
    this.setState({ loadingEducation: value });
  };
  setLoadingApplications = (value: boolean) => {
    this.setState({ loadingApplications: value });
  };
  setThisEducation = (value: education_category | null) => {
    this.setState({ thisEducation: value });
  };
  setThisPeriod = (value: string) => {
    this.setState({ thisPeriod: value });
  };

  FC_OnInstitutionChanged = (institution_id: string) => {
    // LOAD POSITIONS BY INSTITUTION =================
    this.props.FC_GetPositionsByInstitution(
      institution_id,
      (status: boolean, msg: string) => {
        this.setLoading(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      }
    );

    // LOAD SESSION BY INSTITUTION =================
    this.props.FC_GetAllSessionByInstitution(
      institution_id,
      (
        status: boolean,
        errorMsg: string,
        sessionData: SessionPlanInterface[]
      ) => {
        this.setLoading(status);
        if (!status) {
          if (!isEmptyOrSpaces(errorMsg)) {
            this.props.PushPopupAlert({
              title: errorMsg,
              theme: PopupAlertTheme.warning,
            });
          }

          if (sessionData && sessionData.length > 0) {
            let activeSession = sessionData.find((item) => item.is_active);
            if (activeSession) {
              this.setThisPeriod(activeSession._id);
              this.FC_OnSessionChanged(activeSession._id);
            } else {
              this.setThisPeriod(sessionData[0]._id);
              this.FC_OnSessionChanged(sessionData[0]._id);
            }
          }
        }
      }
    );
  };

  FC_OnSessionChanged = (session_id: string) => {
    FC_GetApplicationCountPerPeriod(
      session_id,
      (status: boolean, msg: string, data: DashboardCountType[] | null) => {
        this.setLoadingEducation(status);
        this.setState({ applicationsCounts: data });
        if (!status && msg.length > 0) {
          this.props.PushPopupAlert({
            title: errorToText(msg),
            theme: PopupAlertTheme.danger,
          });
        }
      }
    );

    FC_GetApplicationGeneralCountPerPeriod(
      session_id,
      (
        status: boolean,
        msg: string,
        data: DashboardGeneralCountType | null
      ) => {
        this.setLoadingEducation(status);
        this.setState({ applicationsGeneralCounts: data });
        if (!status && msg.length > 0) {
          this.props.PushPopupAlert({
            title: errorToText(msg),
            theme: PopupAlertTheme.danger,
          });
        }
      }
    );
  };

  componentDidMount = () => {
    if (
      this.props.education_categories === null ||
      this.props.education_categories.length
    ) {
      this.props.loadEducationCategories(
        (status: boolean, data?: education_category[]) => {
          this.setLoading(status);
          if (!status && data && data.length > 0) {
            if (
              FC_HasInstitute(this.props.auth.education_institution_id) ===
              false
            ) {
              this.setThisEducation(data[0]);
              this.FC_OnInstitutionChanged(data[0].education_institute_id);
            } else {
              const _data = data.find(
                (item) =>
                  item.education_institute_id ===
                  this.props.auth.education_institution_id!
              );
              this.setThisEducation(_data ? _data : data[0]);
              this.FC_OnInstitutionChanged(
                this.props.auth.education_institution_id!
              );
            }
          }
        }
      );
    } else if (this.state.thisEducation === null) {
      if (FC_HasInstitute(this.props.auth.education_institution_id) === false) {
        this.setThisEducation(this.props.education_categories[0]);
        this.FC_OnInstitutionChanged(
          this.props.education_categories[0].education_institute_id
        );
      } else {
        this.FC_OnInstitutionChanged(this.props.auth.education_institution_id!);
        const data = this.props.education_categories.find(
          (item) =>
            item.education_institute_id ===
            this.props.auth.education_institution_id
        );
        this.setThisEducation(data ? data : this.props.education_categories[0]);
      }
    }
  };

  setSortOrder = (thisOrder: "ASC" | "DESC") => {
    this.setState({ sortOrder: thisOrder });
  };

  setSortProperty = (thisProperty: string) => {
    this.setState({ sortProperty: thisProperty });
  };

  setIsSorted = (status: boolean) => {
    this.setState({ isSorted: status });
  };

  setSort = (thisProperty: string) => {
    if (
      this.state.sortProperty === thisProperty &&
      this.state.sortOrder === "ASC"
    ) {
      this.setSortOrder("DESC");
    } else {
      this.setSortOrder("ASC");
    }
    this.setSortProperty(thisProperty);
    this.setIsSorted(true);
  };

  FC_Counts = (
    allCounts: { _id: string; position_name: string; applications: number }[]
  ): {
    applications: number;
    positions: number;
  } => {
    let applications: number = 0,
      positions: number = 0;
    for (const key in allCounts) {
      if (allCounts[key].applications > 0) positions += 1;
      applications = applications + allCounts[key].applications;
    }

    return { applications, positions };
  };

  FC_PositionCount = (
    allPosition: PositionAPI[],
    allCounts: DashboardCountType[]
  ): { _id: string; position_name: string; applications: number }[] => {
    let result: { _id: string; position_name: string; applications: number }[] =
      [];
    for (const position_key in allPosition) {
      const elementCounts = allCounts.find(
        (item) => item._id === allPosition[position_key].position_id
      );

      result.push({
        _id: allPosition[position_key].position_id,
        position_name: allPosition[position_key].position_title,
        applications: elementCounts ? elementCounts.total : 0,
      });
    }
    return result;
  };

  render() {
    if (
      this.props.application_sessions === null ||
      this.props.education_categories === null ||
      this.state.applicationsCounts === null ||
      this.props.systemPositions === null ||
      this.state.loading ||
      this.state.loadingEducation ||
      this.state.applicationsGeneralCounts === null
    ) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading Data...
        </div>
      );
    }

    if (this.props.application_sessions.length === 0) {
      return <NoSession />;
    }

    const DATA_POSITIONS = this.FC_PositionCount(
      this.props.systemPositions,
      this.state.applicationsCounts
    );
    const DATA_COUNTS = this.FC_Counts(DATA_POSITIONS);

    return (
      <div>
        {/* HEADER TITLE */}
        <DashBoardTitle
          education_categories={this.props.education_categories}
          loading={this.state.loading}
          loadingApplications={this.state.loadingApplications}
          loadingEducation={this.state.loadingEducation}
          thisEducation={this.state.thisEducation}
          setThisEducation={this.setThisEducation}
          thisPeriod={this.state.thisPeriod}
          setThisPeriod={this.setThisPeriod}
          application_sessions={this.props.application_sessions}
          setLoadingApplications={this.setLoadingApplications}
          reportTitle={"Application Dashboard"}
          allEducation={false}
          FC_OnInstitutionChanged={this.FC_OnInstitutionChanged}
          FC_OnSessionChanged={this.FC_OnSessionChanged}
        />
        {/* END HEADER TITLE */}

        <DashboardLabels
          applicants={this.state.applicationsGeneralCounts.totalAppl}
          applications={DATA_COUNTS.applications}
          shortlisted={0}
          rejected={0}
          positions={DATA_COUNTS.positions}
          allPositions={this.props.systemPositions.length}
        />

        <div className="my-8">
          <table className="w-full shadow" id="report_content">
            <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
              <tr>
                <td className="px-2 py-2">#</td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => this.setSort("position_name")}
                >
                  <div className="flex items-center gap-1">
                    Position <BiSort />
                  </div>
                </td>
                <td
                  className="px-2 py-2 cursor-pointer"
                  onClick={() => this.setSort("applications")}
                >
                  <div className="flex items-center gap-1">
                    Application <BiSort />
                  </div>
                </td>
              </tr>
            </thead>
            <tbody>
              {DATA_POSITIONS.length === 0 ? (
                <tr>
                  <td colSpan={8} className="px-2 py-2">
                    <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                      <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                      <span className="text-sm font-bold">
                        No result found! Please try to search again
                      </span>
                      <div className="w-full mt-2 flex flex-row items-center justify-center">
                        <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                          <li className="underline text-gray-500 cursor-pointer">
                            Filter by session
                          </li>
                        </ul>
                      </div>
                    </div>
                  </td>
                </tr>
              ) : (
                sortThisArray(
                  this.state.isSorted,
                  DATA_POSITIONS,
                  this.state.sortProperty,
                  this.state.sortOrder
                ).map(
                  (
                    item: {
                      _id: string;
                      position_name: string;
                      applications: number;
                    },
                    i: number
                  ) => (
                    <tr
                      key={i + item._id}
                      className={`${
                        i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                      } hover:bg-blue-100 hover:text-blue-700 border-b hover:border-blue-500 text-base text-gray-600 border-gray-300`}
                    >
                      <td className="px-2 py-2">{i + 1}</td>
                      <td className="px-2 py-2">{item.position_name}</td>
                      <td className="px-2 py-2">{item.applications}</td>
                    </tr>
                  )
                )
              )}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  locations,
  schoolLevels,
  dashboard,
  course,
  system_settings,
  sessionPlan,
}: StoreState): {
  auth: Auth;
  locations: RoomLocationInterface[];
  education_categories: education_category[] | null;
  dashboard: DashboardReducerType;
  systemPositions: PositionAPI[] | null;
  course: courseStoreInterface;
  application_sessions: SessionPlanInterface[] | null;
} => {
  return {
    auth: auth,
    locations: locations.roomLocation,
    education_categories: schoolLevels.education_categories,
    dashboard,
    course: course,
    systemPositions: system_settings.systemPositions,
    application_sessions: sessionPlan.allSessionPlans,
  };
};

export const AdminDashboard = connect(mapStateToProps, {
  getAllRoom,
  loadEducationCategories,
  FC_GetPositionsByInstitution,
  FC_GetAllSessionByInstitution,
  GetApplicationPerPeriod,
  loadCourses,
  PushPopupAlert,
})(Index);
